"use client";

import Link from "next/link";
import React, { useRef } from "react";
import { useGSAP } from "@gsap/react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);

const LIFTING_TREATMENTS = [
  {
    id: "ulthera",
    name: "울쎄라 (Ulthera)",
    link: "/signature/ulthera",
    description:
      "마이크로포커스 초음파(MFUS) 기술을 사용하여 피부 표면에 손상을 주지 않으면서 SMAS층(근건막층)까지 에너지를 전달하는 FDA 승인 시술입니다. DeepSEE™ 기술로 실시간 초음파 영상을 통해 피부 깊이를 확인하며 정확하게 시술합니다.",
  },
  {
    id: "shrink",
    name: "슈링크 유니버스 (Shrink Universe)",
    link: "#",
    description:
      "고강도 집속 초음파(HIFU) 에너지를 이용한 2세대 리프팅 시술입니다. MP 모드와 Normal(도트) 모드 두 가지 모드로 시술이 가능하며, 총 7가지 카트리지로 깊이 맞춤화(1.5mm~4.5mm)가 가능합니다.",
  },
  {
    id: "tune",
    name: "튠페이스 (Tune Face)",
    link: "#",
    description:
      "이스라엘 ALMA사에서 개발한 멀티 고주파를 활용한 페이스리프팅 시술로, 40.68MHz의 이상적인 주파수 에너지를 사용하여 피부 속 물분자를 회전시키고, 이로 인한 마찰열로 콜라겐을 자극하고 재생합니다.",
  },
];

const SPECIALITY_ITEMS = [
  {
    id: "effect01",
    title: "높은 고정력과 리프팅효과",
    description: "각 리프팅 시술을 기술적으로 컴바인하여 단 1일만에 원하는 리프팅 효과를 나타낼 수 있습니다.",
  },
  {
    id: "effect02",
    title: "빠른 시술 속도",
    description: "효율적인 시술 방법으로 바쁜 시간 중에도 편안하게 부담없이 받으실 수 있습니다.",
  },
  {
    id: "effect03",
    title: "시술 후 통증 최소화",
    description: "정확한 피부층에 에너지를 전달하면서 빠르게 완성하여 붓기와 멍, 통증이 거의 없는 방법으로 시술하고 있습니다.",
  },
  {
    id: "effect04",
    title: "얼굴형 맞춤시술",
    description: "개인의 골격, 피부 두께, 근육의 움직임까지 분석하여 가장 이상적인 리프팅 벡터를 설계합니다.",
  },
  {
    id: "effect05",
    title: "편안한 시술",
    description: "리프팅에 대한 걱정과 두려움을 최소화하여 편안하게 시술 받으실 수 있습니다.",
  },
];

const FEATURE_ITEMS = [
  {
    num: "01",
    title: "울쎄라 (Ulthera)",
    description: "마이크로포커스 초음파 기술로 SMAS층까지 에너지를 전달하는 FDA 승인 시술",
    link: "/signature/ulthera",
  },
  {
    num: "02",
    title: "슈링크 유니버스",
    description: "고강도 집속 초음파(HIFU) 에너지를 이용한 2세대 리프팅 시술",
    link: "#",
  },
  {
    num: "03",
    title: "튠페이스",
    description: "멀티 고주파를 활용한 페이스리프팅 시술로 3단계 깊이 조절이 가능",
    link: "#",
  },
];

export default function LiftingView() {
  const mainRef = useRef<HTMLElement>(null);
  const wrapRef = useRef<HTMLDivElement>(null);

  // 이미지 로드 실패 시 fallback 처리
  React.useEffect(() => {
    const checkImages = () => {
      const bgImages = document.querySelectorAll<HTMLElement>("._lifting .bg[style*='background-image'], ._lifting .sec3[style*='background-image']");
      bgImages.forEach((img) => {
        const bgUrl = img.style.backgroundImage.match(/url\(["']?([^"')]+)["']?\)/)?.[1];
        if (bgUrl && bgUrl.startsWith("/")) {
          const testImg = new Image();
          testImg.onerror = () => {
            // 이미지가 없으면 fallback 적용 (Kahky Clinic 스타일)
            if (bgUrl.includes("sec1_bg")) {
              img.style.backgroundImage = "url(https://images.unsplash.com/photo-1616394584738-fc6e612e0b0a?q=80&w=2070&auto=format&fit=crop)";
            } else if (bgUrl.includes("sec2_easy")) {
              img.style.backgroundImage = "url(https://images.unsplash.com/photo-1556228578-0d85b1a4d571?q=80&w=2070&auto=format&fit=crop)";
            } else if (bgUrl.includes("sec3_bg")) {
              img.style.backgroundImage = "url(https://images.unsplash.com/photo-1556228578-0d85b1a4d571?q=80&w=2070&auto=format&fit=crop)";
            } else if (bgUrl.includes("speciality_bg")) {
              img.style.backgroundImage = "url(https://images.unsplash.com/photo-1616394584738-fc6e612e0b0a?q=80&w=2070&auto=format&fit=crop)";
            }
          };
          testImg.onload = () => {
            // 이미지가 로드되면 원본 URL 유지
          };
          testImg.src = bgUrl;
        }
      });
    };
    
    // DOM이 렌더링된 후 확인
    const timer = setTimeout(checkImages, 100);
    return () => clearTimeout(timer);
  }, []);

  useGSAP(
    () => {
      // 수평 스크롤 (데스크톱)
      ScrollTrigger.matchMedia({
        "(min-width: 1025px)": () => {
          if (!wrapRef.current) return;

          const horizon = gsap.to("._lifting .horizontal .Wrap", {
            xPercent: -100,
            x: () => window.innerWidth,
            scrollTrigger: {
              pin: "._lifting .horizontal",
              trigger: "._lifting .horizontal",
              start: "left left",
              end: () => `+=${wrapRef.current?.offsetWidth || 0} bottom`,
              scrub: 1,
            },
          });

          // sec1 애니메이션
          gsap.timeline()
            .from("._lifting .sec1 .bg", { scale: 1.2, duration: 2 })
            .from("._lifting .sec1 .lbx", { opacity: 0, xPercent: -100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .rbx", { opacity: 0, xPercent: 100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .tit p", { opacity: 0, y: 40, stagger: 0.2, duration: 0.8 }, "-=80%");

          // sec2 애니메이션
          const tl2 = gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec2",
              start: "left 45%",
              end: "left",
              containerAnimation: horizon,
              toggleActions: "play none none reverse",
            },
          });
          tl2
            .from("._lifting .sec2 .bg", { scale: 1.2, duration: 1.5 })
            .from("._lifting .sec2 .tit h2", { opacity: 0, duration: 0.8, y: 40 }, "-=100%")
            .from("._lifting .sec2 .tit .bx", { opacity: 0, duration: 0.8, y: 40 }, "-=80%");

          // 헤더 색상 변경
          ScrollTrigger.create({
            trigger: "._lifting .sec2",
            start: "left 45%",
            containerAnimation: horizon,
            onEnter: () => {
              document.querySelector("header")?.classList.remove("white");
            },
            onLeaveBack: () => {
              document.querySelector("header")?.classList.add("white");
            },
          });

          // sec3 애니메이션
          const tl3 = gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec3",
              start: "left 90%",
              end: "left",
              containerAnimation: horizon,
              toggleActions: "play none none reverse",
            },
          });
          tl3
            .from("._lifting .sec3 .con .tit > *", { opacity: 0, stagger: 0.2, duration: 0.8, x: -40 })
            .from("._lifting .sec3 .con .sub p > *", { opacity: 0, stagger: 0.2, duration: 0.8, y: 40 }, "-=80%");

          // 마퀴 애니메이션
          const marquees = document.querySelectorAll("._lifting .sec2 .marquee .absol");
          marquees.forEach((marquee) => {
            const marqueeChild = marquee.querySelector(".mar_ch");
            if (!marqueeChild) return;

            const cloneMarqueeTimes = Math.ceil(
              (marquee as HTMLElement).getBoundingClientRect().width /
                (marqueeChild as HTMLElement).getBoundingClientRect().width
            );

            for (let i = 0; i < cloneMarqueeTimes; i++) {
              const childClone = marqueeChild.cloneNode(true);
              marquee.appendChild(childClone);
            }

            gsap.fromTo(
              marquee.querySelectorAll(".mar_ch"),
              { xPercent: 0 },
              {
                xPercent: -100,
                repeat: -1,
                duration: 15,
                ease: "none",
              }
            );
          });
        },
        "(max-width: 1024px)": () => {
          // 모바일 애니메이션
          gsap.timeline()
            .from("._lifting .sec1 .bg", { scale: 1.2, duration: 2 })
            .from("._lifting .sec1 .lbx", { opacity: 0, xPercent: -100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .rbx", { opacity: 0, xPercent: 100, duration: 1.5 }, "-=100%")
            .from("._lifting .sec1 .tit p", { opacity: 0, y: 40, stagger: 0.2, duration: 0.8 }, "-=80%");

          gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec2",
              start: "top center+=20%",
              toggleActions: "play none none reverse",
            },
          })
            .from("._lifting .sec2 .bg", { scale: 1.2, duration: 1.5 })
            .from("._lifting .sec2 .tit h2", { opacity: 0, duration: 0.8, y: 40 }, "-=100%")
            .from("._lifting .sec2 .tit .bx", { opacity: 0, duration: 0.8, y: 40 }, "-=80%");

          gsap.timeline({
            scrollTrigger: {
              trigger: "._lifting .sec3",
              start: "top center+=20%",
              toggleActions: "play none none reverse",
            },
          })
            .from("._lifting .sec3 .con .tit > *", { opacity: 0, stagger: 0.2, duration: 0.8, x: -40 })
            .from("._lifting .sec3 .con .sub p > *", { opacity: 0, stagger: 0.2, duration: 0.8, y: 40 }, "-=80%");
        },
      });

      // Speciality 섹션
      ScrollTrigger.matchMedia({
        "(min-width: 1025px)": () => {
          ScrollTrigger.create({
            trigger: "._lifting .speciality .lbx",
            endTrigger: "._lifting .speciality",
            start: "top top",
            end: "bottom bottom",
            scrub: 1,
            pin: true,
          });

          gsap.to("._lifting .speciality .wrap", {
            scrollTrigger: {
              trigger: "._lifting .speciality .bg",
              endTrigger: "._lifting .speciality",
              start: "top top",
              end: "bottom bottom",
              scrub: 1,
              pin: true,
            },
          });
        },
      });

      const specialityTl = gsap.timeline({
        scrollTrigger: {
          trigger: "._lifting .speciality",
          start: "top center+=20%",
          toggleActions: "play none none reverse",
          onEnter: () => {
            document.querySelector("header")?.classList.remove("white");
          },
          onLeaveBack: () => {
            document.querySelector("header")?.classList.add("white");
          },
        },
      });
      specialityTl
        .from("._lifting .speciality .wrap .tit h2", { opacity: 0, x: 40, duration: 0.8 })
        .from("._lifting .speciality .lbx", { opacity: 0, x: -40, duration: 0.8 }, "-=80%");

      const infoBoxes = document.querySelectorAll("._lifting .speciality .con .rbx .bx");
      infoBoxes.forEach((item) => {
        gsap.timeline({
          scrollTrigger: {
            trigger: item,
            start: "top center+=20%",
            toggleActions: "play none none reverse",
          },
        }).from(item, { opacity: 0, y: 40, duration: 0.8 });
      });

      // Feature 섹션
      gsap.timeline({
        scrollTrigger: {
          trigger: "._lifting .feature",
          start: "top center+=20%",
          toggleActions: "play none none reverse",
        },
      })
        .from("._lifting .feature .tit h2", { opacity: 0, y: 40, duration: 0.8 })
        .from("._lifting .feature .tit p", { opacity: 0, y: 40, duration: 0.8 }, "-=80%");

      const featureBoxes = document.querySelectorAll("._lifting .feature .con .bx");
      featureBoxes.forEach((item, idx) => {
        gsap.timeline({
          scrollTrigger: {
            trigger: item,
            start: "top center+=20%",
            toggleActions: "play none none reverse",
          },
        }).from(item, { opacity: 0, y: 40, duration: 0.8, delay: (idx % 2) * 0.1 });
      });

      gsap.from("._lifting .feature .linkBx a", {
        opacity: 0,
        y: 40,
        duration: 0.8,
        stagger: 0.1,
        scrollTrigger: {
          trigger: "._lifting .feature .linkBx",
          start: "top center+=20%",
          toggleActions: "play none none reverse",
        },
      });
    },
    { scope: mainRef, dependencies: [] }
  );

  return (
    <main ref={mainRef} className="_lifting _s min-h-screen bg-[#f8f6f3] text-[#2c2c2c]">
      {/* 수평 스크롤 섹션 */}
      <section className="horizontal relative w-full overflow-hidden">
        <div ref={wrapRef} className="Wrap relative flex w-max">
          {/* 섹션 1: 히어로 - Kahky 스타일 */}
          <div className="sec1 relative h-screen w-screen flex-shrink-0 flex items-center justify-center overflow-hidden bg-[#f8f6f3]">
            <div
              className="bg absolute inset-0 bg-cover bg-center bg-[#f8f6f3]"
              style={{
                backgroundImage: "url(https://images.unsplash.com/photo-1616394584738-fc6e612e0b0a?q=80&w=2400&auto=format&fit=crop)",
              }}
            />
            <div className="absolute inset-0 bg-gradient-to-b from-[#2c2c2c]/50 via-[#2c2c2c]/30 to-[#2c2c2c]/60" />
            <div className="lbx absolute left-[8%] top-1/2 -translate-y-1/2 z-10">
              <span className="text-[clamp(72px,14vw,140px)] font-en-display font-light tracking-[0.25em] text-[#e8e4dd]">
                FIGURE
              </span>
            </div>
            <div className="rbx absolute right-[8%] top-1/2 -translate-y-1/2 z-10">
              <span className="text-[clamp(72px,14vw,140px)] font-en-display font-light tracking-[0.25em] text-[#e8e4dd]">
                MAKING
              </span>
            </div>
            <div className="tit absolute bottom-[12%] left-1/2 -translate-x-1/2 z-10 text-center max-w-5xl px-8">
              <p className="text-[clamp(18px,2.2vw,24px)] text-[#f5f3ef] leading-[1.85] mb-6 font-light tracking-[0.01em]">
                아름다운 라인을 만드는 "FIGURE MAKING"을 모토로 탄생한
                <br />
                오마쥬의 TOP 시그니처 시술.
              </p>
              <p className="text-[clamp(15px,1.8vw,20px)] text-[#e8e4dd] leading-[1.9] font-light tracking-[0.01em]">
                얼굴의 전반적인 조화, 자연스러움, 각각의 개성을 살리고
                <br />
                단점을 보완하는 방향으로 수술없이 간단한 시술로도
                <br />
                본연의 숨겨진 라인과 아름다움을 재발견.
              </p>
            </div>
            <div className="scrollcircle absolute bottom-20 left-1/2 -translate-x-1/2 z-10">
              <div className="rel relative">
                <img 
                  src="/asset/img/sub/scroll_circle.png" 
                  alt="scroll" 
                  className="w-16 h-16 opacity-50"
                  onError={(e) => {
                    (e.target as HTMLImageElement).style.display = "none";
                  }}
                />
                <span className="absolute inset-0 flex items-center justify-center">
                  <svg
                    id="arrow_down"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 128.66 218.52"
                    className="w-8 h-8"
                  >
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,408.4a61.33,61.33,0,0,1,61.33-61.33"
                      transform="translate(610.96 -192.89)"
                    />
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,195.89V408.4A61.33,61.33,0,0,0-608,347.07"
                      transform="translate(610.96 -192.89)"
                    />
                  </svg>
                </span>
              </div>
            </div>
          </div>

          {/* 섹션 2: 타이틀 - Kahky 스타일 */}
          <div className="sec2 relative h-screen w-screen flex-shrink-0 flex items-center justify-center overflow-hidden bg-[#f8f6f3]">
            <div
              className="bg absolute inset-0 bg-cover bg-center bg-[#f8f6f3]"
              style={{
                backgroundImage: "url(https://images.unsplash.com/photo-1556228578-0d85b1a4d571?q=80&w=2400&auto=format&fit=crop)",
              }}
            />
            <div className="absolute inset-0 bg-gradient-to-b from-[#2c2c2c]/55 via-[#2c2c2c]/35 to-[#2c2c2c]/55" />
            <div className="tit absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 z-10 text-center">
              <h2 className="text-[clamp(72px,14vw,140px)] font-en-display font-light tracking-[0.15em] text-[#e8e4dd] mb-8">
                Lifting
              </h2>
              <div className="bx">
                <span className="text-[clamp(22px,3.5vw,32px)] text-[#d4c4a0] font-light tracking-[0.12em]">리프팅</span>
              </div>
            </div>
            <div className="marquee absolute bottom-0 left-0 w-full h-24 overflow-hidden z-10">
              <div className="absol absolute flex whitespace-nowrap">
                <div className="mar_ch text-[clamp(48px,8vw,100px)] font-en-display font-light text-[#e8e4dd]/20 tracking-[0.18em]">
                  <span>HOMMAGE LIFTING</span>
                </div>
              </div>
            </div>
          </div>

          {/* 섹션 3: 설명 - Kahky 스타일 */}
          <div
            className="sec3 relative h-screen w-screen flex-shrink-0 flex items-center justify-center overflow-hidden bg-cover bg-center bg-[#f8f6f3]"
            style={{
              backgroundImage: "url(https://images.unsplash.com/photo-1556228578-0d85b1a4d571?q=80&w=2400&auto=format&fit=crop)",
            }}
          >
            <div className="absolute inset-0 bg-gradient-to-b from-[#2c2c2c]/60 via-[#2c2c2c]/40 to-[#2c2c2c]/60" />
            <div className="scrollcircle absolute top-20 left-1/2 -translate-x-1/2 z-10">
              <div className="rel relative">
                <img 
                  src="/asset/img/sub/scroll_circle.png" 
                  alt="scroll" 
                  className="w-16 h-16 opacity-50"
                  onError={(e) => {
                    (e.target as HTMLImageElement).style.display = "none";
                  }}
                />
                <span className="absolute inset-0 flex items-center justify-center">
                  <svg
                    id="arrow_down"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 128.66 218.52"
                    className="w-8 h-8"
                  >
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,408.4a61.33,61.33,0,0,1,61.33-61.33"
                      transform="translate(610.96 -192.89)"
                    />
                    <path
                      className="stroke-white stroke-[6] fill-none"
                      d="M-546.63,195.89V408.4A61.33,61.33,0,0,0-608,347.07"
                      transform="translate(610.96 -192.89)"
                    />
                  </svg>
                </span>
              </div>
            </div>
            <div className="con absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 z-10 max-w-5xl px-12">
              <div className="tit mb-16">
                <span className="block text-[clamp(48px,7vw,72px)] font-light text-[#d4c4a0] mb-6 tracking-[0.12em]">02</span>
                <h2 className="text-[clamp(36px,6vw,56px)] font-light text-[#e8e4dd] mb-8 tracking-[0.06em]">
                  리프팅이란?
                </h2>
                <p className="text-[clamp(16px,2vw,22px)] text-[#e8e4dd]/90 leading-[1.9] font-light tracking-[0.01em]">
                  고주파, 초음파, 레이저, 실 등을 이용하여 탄력 있는 V라인을 만들기 위한 최적의 리프팅 컴비네이션.
                  <br />
                  각 리프팅 시술을 기술적으로 컴바인하여 단 1일만에 원하는 리프팅 효과를 나타낼 수 있는
                  <br />
                  원데이익스트림리프팅, 또는 과도한 지방을 분해하고 배출하여 윤곽을 개선해주는 조각주사와 함께 컴바인한
                  <br />
                  '조각술'은 더욱 효과적인 라인을 만들어주는 오마쥬만의 차별화된 특별한 경험이 될 것입니다.
                </p>
              </div>
              <div className="line w-full h-[1px] bg-[#d4c4a0]/30 mb-10" />
              <div className="sub">
                <p className="text-[clamp(16px,2vw,20px)] text-[#e8e4dd]/85 font-light tracking-[0.01em]">
                  <b className="text-[#d4c4a0] font-normal">종류</b>
                  <span className="ml-6 tracking-[0.02em]">
                    울쎄라, 슈링크 유니버스, 튠페이스, 실리프팅, V라인 리프팅, 이중턱 리프팅
                  </span>
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Speciality 섹션 - Kahky 스타일 */}
      <section className="speciality relative min-h-screen overflow-hidden bg-[#f8f6f3]">
        <div
          className="bg absolute inset-0 bg-cover bg-center bg-[#f8f6f3]"
          style={{
            backgroundImage: "url(https://images.unsplash.com/photo-1616394584738-fc6e612e0b0a?q=80&w=2400&auto=format&fit=crop)",
          }}
        />
        <div className="absolute inset-0 bg-gradient-to-b from-[#2c2c2c]/55 via-[#2c2c2c]/35 to-[#2c2c2c]/55" />
        <div className="wrap relative z-10 max-w-[1600px] mx-auto px-[8vw] py-40">
          <div className="tit mb-24">
            <h2 className="text-[clamp(48px,7vw,72px)] font-en-display font-light text-[#e8e4dd] text-center tracking-[0.1em]">Speciality</h2>
          </div>
          <div className="grid lg:grid-cols-2 gap-24 items-start">
            <div className="lbx">
              <p className="text-[clamp(28px,5vw,42px)] font-light text-[#e8e4dd] leading-[1.85] tracking-[0.02em]">
                오마쥬 <span className="text-[#d4c4a0] font-normal">리프팅</span>으로
                <br />
                변하지 않는 아름다움을
                <br />
                만들어 보세요.
              </p>
            </div>
            <div className="con pc">
              <div className="rbx">
                <div className="left">
                  {SPECIALITY_ITEMS.filter((_, idx) => idx % 2 === 0).map((item) => (
                    <div key={item.id} className="bx bg-white/10 backdrop-blur-sm p-6 rounded-lg border border-white/20 mb-8">
                      <div className="btn mb-4">
                        <span className="text-xs text-white/60 uppercase tracking-wider">{item.id}</span>
                      </div>
                      <div className="cont">
                        <h3 className="text-lg font-medium text-white mb-3">{item.title}</h3>
                        <p className="text-sm text-white/80 leading-relaxed">{item.description}</p>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="right">
                  {SPECIALITY_ITEMS.filter((_, idx) => idx % 2 === 1).map((item) => (
                    <div key={item.id} className="bx bg-white/10 backdrop-blur-sm p-6 rounded-lg border border-white/20 mb-8">
                      <div className="btn mb-4">
                        <span className="text-xs text-white/60 uppercase tracking-wider">{item.id}</span>
                      </div>
                      <div className="cont">
                        <h3 className="text-lg font-medium text-white mb-3">{item.title}</h3>
                        <p className="text-sm text-white/80 leading-relaxed">{item.description}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
            <div className="con mo">
              <div className="rbx">
                <div className="left">
                  {SPECIALITY_ITEMS.filter((_, idx) => idx % 2 === 0).map((item) => (
                    <div key={item.id} className="bx bg-[#e8e4dd]/10 backdrop-blur-md p-8 rounded-sm border border-[#d4c4a0]/20 mb-10">
                      <div className="btn mb-5">
                        <span className="text-[11px] text-[#d4c4a0]/60 uppercase tracking-[0.18em] font-light">{item.id}</span>
                      </div>
                      <div className="cont">
                        <h3 className="text-xl font-light text-[#e8e4dd] mb-4 tracking-[0.03em]">{item.title}</h3>
                        <p className="text-[15px] text-[#e8e4dd]/80 leading-[1.85] font-light tracking-[0.01em]">{item.description}</p>
                      </div>
                    </div>
                  ))}
                </div>
                <div className="right">
                  {SPECIALITY_ITEMS.filter((_, idx) => idx % 2 === 1).map((item) => (
                    <div key={item.id} className="bx bg-[#e8e4dd]/10 backdrop-blur-md p-8 rounded-sm border border-[#d4c4a0]/20 mb-10">
                      <div className="btn mb-5">
                        <span className="text-[11px] text-[#d4c4a0]/60 uppercase tracking-[0.18em] font-light">{item.id}</span>
                      </div>
                      <div className="cont">
                        <h3 className="text-xl font-light text-[#e8e4dd] mb-4 tracking-[0.03em]">{item.title}</h3>
                        <p className="text-[15px] text-[#e8e4dd]/80 leading-[1.85] font-light tracking-[0.01em]">{item.description}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Feature 섹션 - Kahky 스타일 */}
      <section className="feature relative py-40 bg-[#f8f6f3]">
        <div className="wrap max-w-[1440px] mx-auto px-[8vw]">
          <div className="tit text-center mb-28">
            <h2 className="text-[clamp(48px,7vw,72px)] font-en-display font-light text-[#2c2c2c] mb-6 tracking-[0.1em]">Feature</h2>
            <p className="text-[clamp(18px,2.2vw,24px)] text-[#6b6b6b] font-light tracking-[0.02em]">
              <span className="font-normal text-[#2c2c2c]">리프팅</span> 시술별 특징
            </p>
          </div>
          <div className="con grid md:grid-cols-3 gap-10 mb-20">
            {FEATURE_ITEMS.map((item) => (
              <Link
                key={item.num}
                href={item.link}
                className="bx group bg-white p-10 rounded-sm border border-[#e5e0d8] hover:border-[#d4c4a0] transition-all duration-500 hover:shadow-[0_8px_30px_rgba(212,196,160,0.15)]"
              >
                <div className="bxCont">
                  <div className="top mb-8">
                    <p className="text-2xl font-light text-[#2c2c2c] mb-4 tracking-[0.03em]">
                      <span className="num text-[#b8a078] mr-3 font-light">{item.num}</span>
                      {item.title}
                    </p>
                    <span className="text-[15px] text-[#6b6b6b] leading-[1.85] block font-light tracking-[0.01em]">{item.description}</span>
                  </div>
                  <div className="bottom">
                    <div className="view flex items-center gap-3 text-[#b8a078] opacity-0 group-hover:opacity-100 transition-opacity duration-500">
                      <span className="text-sm font-light tracking-[0.06em] uppercase">View more</span>
                      <div className="arrow">
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9 5l7 7-7 7" />
                        </svg>
                      </div>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
          <div className="linkBx flex flex-wrap justify-center gap-3">
            {[
              { name: "조각주사", link: "#" },
              { name: "이지실리프팅", link: "#" },
              { name: "티타늄리프팅", link: "#" },
              { name: "콰트로리프팅", link: "#" },
              { name: "원데이익스트림리프팅", link: "#" },
              { name: "B토닝", link: "#" },
            ].map((link) => (
              <Link
                key={link.name}
                href={link.link}
                className={`px-8 py-4 border border-[#d4c4a0]/30 rounded-full text-[14px] font-light tracking-[0.03em] transition-all duration-300 ${
                  link.active
                    ? "bg-[#2c2c2c] text-[#e8e4dd] border-[#2c2c2c]"
                    : "bg-white text-[#6b6b6b] hover:border-[#b8a078] hover:text-[#2c2c2c]"
                }`}
              >
                {link.name}
              </Link>
            ))}
          </div>
        </div>
      </section>
    </main>
  );
}
